body {
 color: red;
}

.draw_content_container {
 padding: 40px 55px 0 55px;
}

.errorNumber {
 background-color: red;
}
.tempButton {
 margin: 15px 0 0 0;
}

.form-row-edit {
 margin-left: 0;
 margin-right: 0;
 display: -ms-flexbox;
 display: flex;
}

.network-wrapper {
 -ms-flex-positive: 1;
     flex-grow: 1;
}

.number-wrapper {
 -ms-flex-positive: 1;
     flex-grow: 1;
}

.custom-button {
 width: 100%;
 font-size: 12px;
 padding: .75rem;
}

.formInput {
 padding: .75rem 0.75rem;
 font-size: 12px;
}

.formWrapper {
 display:-ms-flexbox;
 display:flex;
 -ms-flex-pack:center;
     justify-content:center;
 -ms-flex-align:center;
     align-items:center;
}

.dropdown {
 display: table;
 position: relative;
 font-size: 14px;
 font-family: "Arial";
}
.dropdown-button {
 background: #3498db;
 min-width: 100px;
 color: #fff;
 letter-spacing: 0.025rem;
 -webkit-box-sizing: border-box;
         box-sizing: border-box;
 padding: 5px 30px 5px 20px;
 position: relative;
 cursor: pointer;
 -webkit-transition: background .3s ease;
 -o-transition: background .3s ease;
 transition: background .3s ease;
}
.dropdown-button:hover {
 background: #2980b9;
 -webkit-transition: background .3s ease;
 -o-transition: background .3s ease;
 transition: background .3s ease;
}
.triangle {
 font-size: 50%;
 position: absolute;
 right: 10px;
 top: -4px;
 bottom: 0;
 margin-top: auto;
 margin-bottom: auto;
 height: 5px;
 color: #fff;
}
.dropdown ul {
 padding: 0;
 list-style: none;
 -webkit-box-shadow: 0px 2px 6px 0 rgba(0,0,0,0.2);
         box-shadow: 0px 2px 6px 0 rgba(0,0,0,0.2);
 position: absolute;
 left: 0;
 margin-top: 2px;
 top: 100%;
 min-width: 90%;
 color: black;
}
.dropdown li {
 background: #fff;
 padding: 8px 10px 8px 15px;
 -webkit-box-sizing: border-box;
         box-sizing: border-box;
 cursor: pointer;
 -webkit-transition: background .2s ease;
 -o-transition: background .2s ease;
 transition: background .2s ease;
}
.dropdown li:hover {
 background: #f6f6f6;
 -webkit-transition: background .2s ease;
 -o-transition: background .2s ease;
 transition: background .2s ease;
}
.dropdown ul {
 visibility: hidden;
 padding: 0;
 list-style: none;
 -webkit-box-shadow: 0px 2px 6px 0 rgba(0,0,0,0.2);
         box-shadow: 0px 2px 6px 0 rgba(0,0,0,0.2);
 position: absolute;
 left: 0;
 margin-top: 2px;
 top: 100%;
 min-width: 90%;
}


